import QRCodeScanner from "../pages/QRCodeScanner";
import Error from "../pages/NotFound";
import QRCodeScanList from "../pages/QRCodeScanList";


/*****Auth access******/
const Themeroutes = [
    {
        path: "/test",exact:true,
        element: <QRCodeScanner />  // Render Home component for root path
    },
    {
        path: "/qrcode/list",
        element: <QRCodeScanList />  // Render Home component for root path
    },

    {
        path: "*",  // Wildcard route to handle 404 errors (any undefined routes)
        element: <Error />  // Render NotFound component for any other path
    }
];

export default Themeroutes;
