import React, {useEffect, useState} from 'react';
import {Table} from 'antd';
import axios from "axios";



const QrCodeScanList = () => {
    const columns = [
        {
            title: 'Name',
            dataIndex: 'name',
            key: 'name',

        },
        {
            title: 'Age',
            dataIndex: 'age',
            key: 'age',
        },
        {
            title: 'Address',
            dataIndex: 'address',
            key: 'address',
        },

    ];

    const [data, setData] = useState([]);

    const [loading, setLoading] = useState(false);

    useEffect(() => {
        const fetchData = async () => {
            setLoading(true); // Start loading
            try {
                const response = await axios.get("https://sandbox.haselnovin.com/api/admin/v1/exhibition/list");
                // Assuming the response contains data in response.data
                const formattedData = response.data.map((item, index) => ({
                    ...item,
                    key: index, // Ensure each row has a unique key
                }));
                setData(formattedData); // Update state with the transformed data
            } catch (err) {
                console.error("Error fetching data:", err);
            } finally {
                setLoading(false); // Stop loading
            }
        };

        fetchData();
    }, []);



    return (<Table loading={loading} columns={columns} dataSource={data}/>)
};
export default QrCodeScanList;